import React   from "react" 
import axios from 'axios'

const Name = ({person , deletePerson}) =>{
  return(
    <p>
    {person.name} {person.number} <button onClick={deletePerson}>delete</button>
    </p>
  )
}


const Display = ({filteredRecords}) =>{

const deletePerson = ({person}) =>{
    if (window.confirm("Delete " + person.name)) { 
      axios.delete(`http://localhost:3001/persons/${person.id}`,person)
    }
  }
 return(
   <div>
        {filteredRecords().map(person => (
          <Name key={person.number} person={person} deletePerson ={() => deletePerson({person})}/>
        ))}
    </div>
 )
}

export default Display