import React, { useState, useEffect } from "react";
import Filter from "./Components/FilterNames"
import AddPerson from "./Components/AddName"
import Display from "./Components/Display"
import personService from './services/persons'
import Notification from './Components/alert'

const App = () => {
  const [persons, setPersons] = useState([]);
  const [newName, setNewName] = useState("");
  const [newNumber, setNewNumber] = useState("");
  const [search,setSearch] = useState("")
  const [errorMessage, setErrorMessage] = useState({
    message : null,
    classValue : "improvedMessage"
  })

  const filteredRecords = () =>{
    return  persons.filter((person)=>
  { return person.name.toLowerCase().indexOf(search.toLowerCase()) !== -1 }
  )
  }
  
  useEffect(()=>{
    personService.getAll().then(initialData =>{
        setPersons(initialData)
      }
    )
  },[persons])
  
  
  const handleOnNameChange = event => {
    setNewName(event.target.value);
  };
  const handleOnNumberChange = event => {
    setNewNumber(event.target.value);
  };
  const handleOnFieldChanged = event => {
    setSearch(event.target.value)
  };

  const addName = event => {
    event.preventDefault();
    const nameObject = {
      name: newName,
      number: newNumber
    };
    const names = persons.filter(person => person.name===nameObject.name && person.number!==nameObject.number)
    const x= persons.filter(person => person.name===nameObject.name && person.number===nameObject.number)
    
    if (x.length>0) {
      alert(`${newName} is already added to phonebook`);
    } 
    else if(names.length>0){
      if (window.confirm(`${newName} already added to the phonebook, replace the old number with the new number`)) { 
        personService.update(names[0].id,nameObject).then(returnedData =>
          setPersons(persons.map(person => person.id !==names.id ? person:returnedData)
          )
        ).catch(error =>{
          setErrorMessage({message :`Information of ${nameObject.name} has already been removed from server`, classValue : "improvedError"})
          setTimeout(() =>{
            setErrorMessage({message : null ,classValue:"improvedMessage"})
          },5000)
        })
    }
    }
    else {
      personService.create(nameObject).then(returnedData =>{
        setPersons(persons.concat(returnedData));
        setErrorMessage({message : `Added ${nameObject.name}`,classValue: "improvedMessage"})
        setTimeout(()=>{
          setErrorMessage({message : null ,classValue:"improvedMessage"})
        },5000)
      })
      
    }
    setNewName("");
    setNewNumber("");
  };
  


  return (
    <div>
      <h2>Phonebook</h2>
      <Notification message = {errorMessage.message} classValue={errorMessage.classValue}/>
      <Filter search={search} onChange={handleOnFieldChanged}/>
      <h2>Add a new person</h2>
      <AddPerson name={newName} number={newNumber} onChangeName={handleOnNameChange} onChangeNumber={handleOnNumberChange} onClick={addName}/>   
      <h2>Numbers</h2>
      <Display filteredRecords={filteredRecords} />
    </div>
  );
};

export default App;